:root {
  font-size: 17pt;
  font-family: 'Sahitya', serif;
}

body {
  width: 100vw;
  min-height: 100vh;

  // background-color: #eee;

  display: flex;
  justify-content: center;
  align-items: center;
  overflow-x: hidden;

  .container {
    // background-color: #ccc;
    min-height: 100vh;
    max-width: 1000px;
    width: 100vw;
    position: relative;
    z-index: 0;

    * {
      z-index: 2;
    }

    img,
    canvas {
      max-width: 100%;
      object-fit: cover;
      margin: auto;
      display: block;

      &.comic {
        position: absolute;
        z-index: 1;
        width: 500px;
        display: inline-block;

        &#comic-add {
          right: 0;

          height: 400px;
          object-position: top;
        }
        &#comic-solved {
          right: 0;

          height: 300px;
          object-position: top;
        }
      }
    }
  }

  img.sampling-img {
    position: absolute;
    width: 50% !important;
    right: 0;
  }
  img.sampling-img-large {
    width: 70%;
  }

  #python-add {
    width: 300px;
  }

  #code-line pre {
    width: 50%;
  }

  .wrap-right {
    width: 50%;
    right: 0;
  }

  #circle-img {
    width: 110%;
    height: 500px;
  }

  .no-border {
    border: none;
  }

  p {
    margin: 3rem 1rem;
    text-align: justify;
  }

  .large-break {
    height: 5rem;
  }

  .no-mg-bt {
    margin-bottom: 0;
  }
  .no-mg-tp {
    margin-top: 0;
  }
  .no-mg-lf {
    margin-left: 0;
  }
  .no-mg-rg {
    margin-left: 0;
  }

  .floating {
    margin: 2rem 5rem;
  }

  .no-brk {
    white-space: nowrap;
  }

  #screen {
    width: 50%;
    display: inline-block;
  }

  .sketched {
    background: rgb(40, 44, 52);
    color: whitesmoke;
    border: rgb(40, 44, 52) solid 10px;
    border-top-left-radius: 255px 15px;
    border-top-right-radius: 15px 225px;
    border-bottom-right-radius: 225px 15px;
    border-bottom-left-radius: 15px 255px;
  }

  iframe {
    border-radius: 20px;
    border: 5px solid #222;
    padding: 10px;
  }

  #proof {
    position: absolute;
    right: 2rem;
    margin: 0;
    width: 75%;

    h3 {
      font-family: 'Coming Soon', cursive;
      text-align: center;
      font-size: 1.5rem;
      margin: 0.8rem 0 0 0;
    }

    p {
      margin: 0 2rem 2rem 2rem;
    }
  }

  .compare {
    margin: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;

    img {
      width: 45%;
    }
  }

  .animation {
    position: relative;
    padding-bottom: 56.25%;
    iframe {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
  }

  .author {
    display: flex;
    justify-content: space-around;
    align-items: center;
    h3 {
      font-family: 'MonteCarlo', cursive;
    }
    .spacer {
      width: 50%;
      height: 3px;
      background-color: #000;
    }

    button,
    a {
      background-color: #fff;
      border: none;
      color: black;
      text-decoration: none;
      display: inline-block;
      font-size: 30px;
      padding: 10px;
    }
  }

  code {
    padding: 2rem;
    border-radius: 20px;
  }
}

.katex {
  font-size: 1rem !important;
}
